import React from 'react';
import Layout from "../components/Layout";
import Hero from "../components/Hero";
import withTrans from '../i18n/withTranslation';


const AboutPage = ({ data, t }) => {

  const getContent = (translation) => {
    return { __html: `${t(translation)}` }
  }

  return (
    <>
      <Layout
        title={t('site.pages.AboutPage.meta.title')}
        description={t('site.pages.AboutPage.meta.description')}
      >
        <Hero
          isDark={true}
          title={t('site.pages.AboutPage.title')}
          subtitle={t('site.pages.AboutPage.subtitle')}
          alt={t('site.pages.AboutPage.headerAlt')}
          img={data.header.childImageSharp.sizes.src}
        />

        <div className="container my-5">
          <div dangerouslySetInnerHTML={getContent('site.pages.AboutPage.content')}></div>
        </div>
      </Layout>
    </>
  )
}

export const query = graphql`
  query AboutPage {
    header: file(relativePath: {eq: "about-hero.jpg"}) {
      childImageSharp {
        sizes(quality: 100) {
          src
        }
      }
    }
  }
`;

export default withTrans(AboutPage);